.home-img-block {
  .img-wrap{
    margin: -7vw -24px 20px;
    @media all and (min-width: 475px) {
      margin: -8vw -24px 20px;
    }
    @media all and (min-width: 610px) {
      margin: -50px -24px 20px;
    }

    img{
      max-width: 100%;
      height: auto;
      @media all and (min-width: 960px) {
        max-width: 50%;
        position: absolute;
        top: calc(50% - 250px);
        padding-left: 40px;
      }
      @media all and (min-width: 1545px) {
        right: 0;
      }

    }
  }
}

.home-text-block {
  @media all and (min-width: 600px) {
    //margin-top: 182px;
    //width: 60%;
  }
}

.main-heading {
  @include fluid-type(48px, 80px);
  font-family: $additional-font;
  padding-bottom: 24px;
  line-height: 48px;
  font-weight: 400;
  color: $light-font-color;
  @media all and (min-width: 500px) {
    line-height: 60px;
  }
  @media all and (min-width: 960px) {
    margin-top: 8vw;
    line-height: 80px;
  }
  @media all and (min-width: 1400px) {
    margin-top: 182px;
  }
}

.home-subtitle {
  padding-bottom: 36px;
  font-family: $main-font;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: $primary-green;
  @media all and (min-width: 960px) {
    padding-bottom: 60px;
  }
}

.home-description {
  padding-bottom: 64px;
  font-family: $main-font;
  font-size: 18px;
  font-weight: 400;
  line-height: 28px;
  color: $light-font-color;
}

.text-caption {
  padding-bottom: 80px;
  font-family: $main-font;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  color: $light-font-color;
  @media all and (min-width: 960px) {
    padding-bottom: 75px;
    line-height: 28px;
  }
  @media all and (min-width: 1200px) {
    padding-bottom: 202px;
  }
}

.red {
  color: red;
}

.blue {
  color: blue;
}

