.tooltip {
  position: relative;
  outline: none;
}

.tooltip:after {
  content: attr(data-description);
  box-sizing: border-box;
  display: block;
  //background:rgba(133, 167, 94, 0.68);
  background: rgba(115, 164, 27, 0.56);
  color: $input-color;
  padding: 10px;
  position: absolute;
  //left:100%;
  //top:-20px;
  margin-left: -14px;
  width: 100px;
  height: 44px;
  line-height: 25px;
  border-radius: 8px;
  opacity: 0;
  transition: .25s ease-in-out;
}

.tooltip.tooltip-centre:after {
  //left:100%;
  margin-left: -38px;
}

.tooltip:before {
  content: '';
  display: block;
  position: absolute;
  top:22px;
  left: 50%;
  margin-left: -8px;
  height: 0;
  width: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid rgba(115, 164, 27, 0.56);
  transition: .25s ease-in-out;
  opacity: 0;
}

.tooltip:hover:before {
  opacity: 1;
  top: 22px;
}

.tooltip:hover:after {
  opacity: 1;
  top: 30px;
}
